@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
.Loading-loading {
  display: block;
  border: 2px solid #000;
  border-right-color: transparent;
  border-radius: 50%;
  animation: rotate 1s infinite linear;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

